import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { AppService } from '../../services/app.service';

@Component({
    selector: 'app-one-task',
    templateUrl: './one-task.component.html',
    styleUrls: ['./one-task.component.scss']
})
export class OneTaskComponent implements OnInit {

    constructor(private route: ActivatedRoute, private service: AppService) { }

    id: number;
    task: any;

    ngOnInit() {
        this.route.params.subscribe(params => {
            this.id = +params['id'];
            this.service.getAllTasks(1).subscribe((result) => {
                const allTasks = result.json().message.tasks;
                for (const task of allTasks) {
                    if (task.id === this.id) {
                        this.task = task;
                    }
                }
                console.log(this.task);
            });
        });
    }
}



