import { Component, OnInit } from '@angular/core';

import { AppService } from '../../services/app.service';

import { ActivatedRoute, Router } from '@angular/router';

@Component({
    selector: 'app-list-tasks',
    templateUrl: './list-tasks.component.html',
    styleUrls: ['./list-tasks.component.scss']
})
export class ListTasksComponent implements OnInit {

    constructor(private service: AppService, private route: ActivatedRoute, private router: Router) { }

    countPages: number;
    arrPages: any = [];
    page: number;
    filter: any = null;
    selectOption: any;

    ngOnInit() {
        setTimeout(() => {
            if (localStorage.getItem('user')) {
                const user = JSON.parse(localStorage.getItem('user'));
                this.service.login(user.admin, user.password);
            }
        }, 0);

        this.route.queryParams.subscribe(params => {
            this.page = params['page'] === undefined ? 1 : +params['page'];
            if (this.service.tasks === null) {
                this.selectOption = (params['sort_field'] === undefined || params['sort_direction'] === undefined)
                    ? null : params['sort_field'] + '_' + params['sort_direction'];
                this.filter = this.selectOption !== null ? this.selectOption.split('_') : null;
                const filterString = this.filter !== null ? '&sort_field=' + this.filter[0] + '&sort_direction=' + this.filter[1] : '';
                this.service.getAllTasks(this.page, filterString !== null ? filterString : '').subscribe((result) => {
                    this.service.tasks = result.json().message.tasks;
                    this.countPages = Math.ceil(result.json().message.total_task_count / 3);
                    for (let i = 0; i < this.countPages; i++) {
                        this.arrPages[i] = i + 1;
                    }
                });
            }
        });
    }

    trackById(index, item): void {
        return item.id;
    }

    changePage(page: number) {
        if (this.filter === null) {
            this.service.getAllTasks(page, '').subscribe((result) => {
                this.router.navigate(
                    ['/tasks'],
                    {
                        queryParams: {
                            page: page
                        }
                    });
                this.service.tasks = result.json().message.tasks;
            });
        } else {
            this.service.getAllTasks(page, '&sort_field=' + this.filter[0] + '&sort_direction=' + this.filter[1])
                .subscribe((result) => {
                    this.service.tasks = result.json().message.tasks;
                    this.router.navigate(
                        ['/tasks'],
                        {
                            queryParams: {
                                page: page,
                                sort_field: this.filter[0],
                                sort_direction: this.filter[1]
                            }
                        });
                });
        }
    }

    changeFilter(filter: string) {
        if (filter === 'none') {
            this.filter = null;
            this.service.getAllTasks(this.page, '')
                .subscribe((result) => {
                    this.service.tasks = result.json().message.tasks;
                    this.router.navigate(['/tasks'], { queryParams: { page: this.page } });
                });
        } else {
            this.filter = filter.split('_');
            this.service.getAllTasks(this.page, '&sort_field=' + this.filter[0] + '&sort_direction=' + this.filter[1])
                .subscribe((result) => {
                    console.log(this.page, this.filter[0], this.filter[1]);
                    this.service.tasks = result.json().message.tasks;
                    this.router.navigate(
                        ['/tasks'],
                        {
                            queryParams: {
                                page: this.page,
                                sort_field: this.filter[0],
                                sort_direction: this.filter[1]
                            }
                        });
                });
        }
    }

    onEdit(task) {
        this.service.editTask(task).subscribe((result) => {
            console.log(result.json());
        });
    }
}



