import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { Ng2ImgToolsService } from 'ng2-img-tools';

import { AppService } from '../../services/app.service';

@Component({
    selector: 'app-create-task',
    templateUrl: './create-task.component.html',
    styleUrls: ['./create-task.component.scss']
})
export class CreateTaskComponent implements OnInit {
    username = '';
    email = '';
    text = '';
    image: Blob;
    imageFormatError = false;

    response: any = {
        message: {
            username: '',
            email: '',
            text: '',
            image: '',
            status: ''
        }
    };

    resizedExactCroppedImage: any = null;
    resizedExactCroppedImageTrusted: any = null;

    constructor(
        private ng2ImgToolsService: Ng2ImgToolsService,
        private sanitizer: DomSanitizer,
        private service: AppService
    ) { }

    ngOnInit() {
        this.service.tasks = null;
    }

    fileChange(event: any) {
        const file = event.target.files;
        if (file.length > 0 && (file[0].type === 'image/jpeg' || file[0].type === 'image/png' || file[0].type === 'image/gif')) {
            this.imageFormatError = false;
            this.ng2ImgToolsService.resizeExactCrop([event.target.files[0]], 320, 240).subscribe(result => {
                this.image = result;
                this.resizedExactCroppedImage = window.URL.createObjectURL(result);
                this.resizedExactCroppedImageTrusted = this.sanitizer.bypassSecurityTrustUrl(this.resizedExactCroppedImage);
                window.URL.revokeObjectURL(this.resizedExactCroppedImageTrusted);
            }, error => {
                console.error('Resize exact crop error:', error);
            }
            );
        } else {
            this.image = null;
            this.resizedExactCroppedImageTrusted = null;
            this.imageFormatError = true;
        }
    }

    sendData(): void {
        const formData = new FormData();
        formData.append('username', this.username);
        formData.append('email', this.email);
        formData.append('text', this.text);
        formData.append('image', this.image);

        this.service.createTask(formData).subscribe((result) => {
            this.response = result.json();
            console.log(result);
        });
    }
}



